.products-list {
  display: grid;
  grid-template-columns: repeat(3,1fr);
  column-gap: 85px;
  list-style: none;
  padding: 0;
  margin: 0;

  @include vp-1439 {
    display: flex;
    flex-wrap: wrap;
  }

  @include vp-1023 {
    row-gap: 30px;
    justify-content: center;
  }
}

.products-list li {
  width: 100%;
  max-width: 350px;
  min-height: 650px;
  background-image: url('../img/product-1.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;

  @include vp-1023 {
    max-width: 670px;
    min-height: 250px;
    background-image: url('../img/tablet-product-1.png');
  }

  @include vp-767 {
    max-width: 670px;
    background-image: url('../img/mobile-product-1.png');
  }

}

.products-list li:nth-of-type(2) {
  background-image: url('../img/product-2.png');

  @include vp-1023 {
    background-image: url('../img/tablet-product-2.png');
  }

  @include vp-767 {
    background-image: url('../img/mobile-product-2.png');
  }
}

.products-list li:last-of-type {
  background-image: url('../img/product-3.png');

  @include vp-1023 {
    background-image: url('../img/tablet-product-3.png');
  }

  @include vp-767 {
    background-image: url('../img/mobile-product-3.png');
  }
}

.products-list a {
  width: 100%;
  height: 100%;
  padding-top: 50px;
  padding-left: 40px;
  display: block;
  font-family: $ff-robotocondensed;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: $color-default-white;
  text-decoration: none;

  &::after {
    position: absolute;
    z-index: 0;
    content: "";
    width: 60px;
    height: 60px;
    right: 0;
    bottom: 0;
    background-color: $color-neon-blue;
    background-image: url('../img/svg/arrow.svg');
    background-repeat: no-repeat;
    background-position: center;

    @include vp-767 {
      width: 40px;
      height: 40px;
    }
  }

  @include hover-focus {
    box-shadow: inset -10px -5px 10px 5px $color-default-black;

    &::after {
      box-shadow: inset 5px 5px 10px 5px $color-default-black;
    }
  }

  @include vp-767 {
    padding-top: 30px;
    padding-left: 30px;
    font-size: 18px;
    line-height: 30px;
  }
}
