.accordion-list {
  display: inline-flex;
  max-height: 140px;
  flex-direction: column;
  flex-wrap: wrap;
  list-style: none;
  column-gap: 30px;
  padding: 0;
  margin: 0;
  color: rgba(255, 255, 255, 0.7);

  @include vp-1439 {
    max-height: max-content;
    flex-wrap: nowrap;
  }

  @include vp-1023 {
    max-height: max-content;
    flex-wrap: nowrap;
  }

  @include vp-767 {
    max-height: max-content;
    flex-wrap: nowrap;
    padding-left: 25px;
  }

  & li:not(:first-of-type),
  & li:not(:last-of-type) {
    margin-bottom: 10px;
  }

  & li:nth-child(4) {
    margin-bottom: 0;
  }

  & p {
    color: rgba(255, 255, 255, 0.7);
  }

  & a {
    display: block;
    font-family: $ff-ptmono;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: rgba(255, 255, 255, 0.7);

    @include hover-focus {
      background-color: #ccc;
      color: $color-font-black;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 25px;
    }
  }


}
