.products {
  background: $bg-color-dark;
  padding-top: 70px;
  padding-bottom: 140px;

  @include vp-767 {
    padding-top: 50px;
    padding-bottom: 86px;
  }
}

.products h2 {
  font-family: $ff-robotocondensed;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $color-default-white;
  margin-bottom: 50px;
  margin-top: 0;

  @include vp-767 {
    font-size: 28px;
    line-height: 39px;
    margin-bottom: 40px;
  }
}

.products span:first-of-type {
  @include vp-767 {
    display: none;
  }
}

.products span:last-of-type {
  display: none;

  @include vp-767 {
    display: inline-block;
  }
}
